.Input {
	margin-bottom: 3.2rem;
	cursor: text;
}

.TextField {
	width: 100%;
	height: 100%;
	color: var(--text-primary);
	border: none;
	box-shadow: 0px 0px 0px 1.5px var(--text-secondary) inset;
	border-radius: 0.6rem;
	font-family: "Nunito", sans-serif;
	font-size: 1.6rem;
	background-color: transparent;
	padding: 0.7rem;
	cursor: text;
	transition: box-shadow var(--transition-speed) ease;
	resize: none;
}

.LabelContainer {
	cursor: text;
	padding-bottom: 0.75rem;
}

.LabelText {
	color: var(--text-secondary);
	text-transform: uppercase;
	font-size: 1.4rem;
	cursor: text;
}

.ErrorMessage {
	font-size: 1.4rem;
	cursor: text;
	color: var(--pink-dark);
}

.TextField:focus {
	outline: none;
	cursor: text;
	border: none;
	box-shadow: 0px 0px 0px 3px var(--blue-light) inset;
}

.TextField::-webkit-scrollbar {
	width: 0.4rem;
}

.TextField::-webkit-scrollbar-track {
	background: #1e1e24;
}

.TextField::-webkit-scrollbar-thumb {
	background: var(--text-primary);
}
