
.Label {
	font-size: 1.8rem;
	color: var(--text-secondary);
	text-align: center;
	margin-bottom: -2rem;
}

.Loader {
   font-size: 10px;
   margin: 50px auto;
   text-indent: -9999em;
   width: 12rem;
   height: 12rem;
   border-radius: 50%;
   background: var(--pink-dark);
   background: -moz-linear-gradient(left, var(--pink-dark) 10%, rgba(255, 255, 255, 0) 42%);
   background: -webkit-linear-gradient(left, var(--pink-dark) 10%, rgba(255, 255, 255, 0) 42%);
   background: -o-linear-gradient(left, var(--pink-dark) 10%, rgba(255, 255, 255, 0) 42%);
   background: -ms-linear-gradient(left, var(--pink-dark) 10%, rgba(255, 255, 255, 0) 42%);
   background: linear-gradient(to right, var(--pink-dark) 10%, rgba(255, 255, 255, 0) 42%);
   position: relative;
   -webkit-animation: load3 1.4s infinite linear;
   animation: load3 1.4s infinite linear;
   -webkit-transform: translateZ(0);
   -ms-transform: translateZ(0);
   transform: translateZ(0);
 }
 .Loader:before {
   width: 50%;
   height: 50%;
   background: var(--pink-dark);
   border-radius: 100% 0 0 0;
   position: absolute;
   top: 0;
   left: 0;
   content: '';
 }
 .Loader:after {
   background: var(--bg-secondary);
   width: 75%;
   height: 75%;
   border-radius: 50%;
   content: '';
   margin: auto;
   position: absolute;
   top: 0;
   left: 0;
   bottom: 0;
   right: 0;
 }
 @-webkit-keyframes load3 {
   0% {
     -webkit-transform: rotate(0deg);
     transform: rotate(0deg);
   }
   100% {
     -webkit-transform: rotate(360deg);
     transform: rotate(360deg);
   }
 }
 @keyframes load3 {
   0% {
     -webkit-transform: rotate(0deg);
     transform: rotate(0deg);
   }
   100% {
     -webkit-transform: rotate(360deg);
     transform: rotate(360deg);
   }
 }
 